
.weather-list{
    display: flex;
    justify-content: space-between;
    list-style-type: none;
    margin-bottom: 72px;
}

.weather-item{
    width: 120px;
    height: 170px;
    padding-top: 18px;
    color: #E7E7EB;
    background-color: #1E213A;
    cursor: pointer;
}

.weather-title{
    font-size: 16px;
    font-weight: 500;
    text-align: center;
    margin-bottom: 10px;
}

.weather-img{
    display: block;
    height: 90px;
    margin: 0 auto;
}

.weather-tempature{
    display: flex;
    justify-content: center;
}

.day, .night{
    font-size: 16px;
    font-weight: 500;
    margin-bottom: 10px;
}

.day{
    margin-right: 16px;
}

.night{
    color: #A09FB1;
}